*{
    margin: 0;
    padding: 0;
    font-family: 'Nunito Sans', sans-serif;
}
.flex{
    display: flex;
}
.items-center{
    align-items: center;
}
.justify-center{
    justify-content: space-between;
}

/* List styling */
.list{
    list-style: none;
    font-size: 17px;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}

/* Logo */
#logo{
    margin:15px 30px;
}
.logo{
    height:125.16px;
    width: 310px;
}

/* Navbar */
#navbar ul li a{
    color: white;
    padding: 10px 25px;
    text-decoration: none;
    margin: auto;
    transition: all 0.3s ease;
}
#navbar{
    position: top;
    /* justify-content: center; */
    /* align-items: center; */
    position: sticky;
    top: 0;
    /* cursor: pointer; */
}
#navbar::before{
    content: "";
    background-color: black;
    position: absolute;
    top:0px;
    left:0px;
    height: 100%;
    width:100%;
    z-index: -1;
    opacity: 0.7;
}
.item{
    display: flex;
    align-items: center;
}
.social{
    display: flex;
    align-items: center;
}
.list2{
    list-style: none;
    font-size: 12px;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 600;
}
.list2 li{
    padding:0;
}
.background{
    background: rgba(0, 0, 0, 0) url("./images/cover6.jpeg");
    background-size: cover;
    background-position: center;
    background-blend-mode: darken;
    height: auto;
    /* margin: -10px 0; */
}
.sectionfirst{
    height: 100vh;
    margin-top: -160px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* width:100%; */
}
/* .mainbody{
    display: flex;
} */
.text{
    color: white;
    margin: auto;
    text-align: center;
}
.text h1{
    font-size: 55px;
    font-weight: 400;
    padding: 20px 0;
    font-family: 'Nunito Sans', sans-serif;
}
.text h3{
    font-size: 25px;
    font-weight: 400;
    font-family: 'Nunito Sans', sans-serif;
}
.buttn{
    text-align: center;
    padding: 30px 0;
}
.buttn a button{
    margin: auto ;
    padding: 20px 40px;
    text-align: center;
    font-size: 14px;
    font-weight: 800;
    color: #303030;
    border-radius: 200px;
    border-width: 0ch;
    font-family: 'Nunito Sans', sans-serif;
}
.buttn a button:hover{
    color:#9CCD7E;
    cursor: pointer;
}
.slider{
    /* display: flex; */
    text-align: center;
    align-items: center;
    /* position: 10vh; */
    margin: auto;
    /* padding: -100px; */
}
.thumbnail{
    border-radius: 50%;
}
.preview .active{
    /* display: block;   */
    border: 2px solid white;
    /* width: 100px; */
    
    border-color: white;
}
.arrow{
    align-items: center;
    justify-content: center;
    display: flex;
    padding: 0 250px;
}
/* #prev{
    padding-right: 100px;
} */
.section2{
    background-color: #1b1b1b;
    color: white;
}
.section3{
    background-color: #1b1b1b;
    color: white;
}
.heading{
    text-align: center;
    font-size: 24px;
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 200;
    padding: 40px 0;
    padding-top:100px ;
}
.infopara{
    font-size: 20px;
    inline-size: 570px;
    line-height: 1.6;
    font-family: 'Nunito Sans', sans-serif;
    padding: 0 40px;
}
.info{
    margin: auto;
    align-items: center;
    justify-content: center;
}
.numbers{
    align-items: center;
    /* text-align: center; */
    justify-content: center;
    display: flex;
    margin: auto;
}
.counter{
    display: flex;
    padding: 30px 100px;
}
.counter h1{
    font-size: 20px;
    font-weight: 400;
}
.counter h1 span{
    font-size: 70px;
}
.shots{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 2rem;
    grid-row-gap: 6rem;
    margin:0 200px;
    padding-bottom: 30px;
}
.story{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2rem;
    grid-row-gap: 6rem;
    margin:0 200px;
    padding-bottom: 30px;
}
.story1{
    /* background-color: black; */
    background: rgba(0, 0, 0, 0.3);
    border-radius:10px ;
    text-align: center;
    padding-bottom: 30px;
}
.story1 img{
    border-top-right-radius:10px ;
    border-top-left-radius: 10px;
}
.story1 p{
    inline-size: 480px;
    /* padding: 40px; */
    margin: auto;
    line-height: 1.7;
}
.story1 h1{
    font-size: 24px;
    padding: 20px;
    font-weight: 600;
}
.story1 hr{
    opacity: 0.3;
    margin: 25px 40px ;
}
.story1 h3{
    text-align: left;
    padding-left:40px ;
    font-size: 12px;
    font-weight: 600;
}
.foot{
    /* align-items:center; */
    display: flex;
    justify-content: space-between;
    background: rgba(0, 0, 0, 0.93);
    padding: 10vh;
}
